<template>
  <div id="app">
    <Header @change-card="change" />
    <div v-if="load == true">
      <Card :dn="lastNow" :df="lastForecast" :text="textForecast" />
    </div>
    <div v-if="selected–°ard == 1">
      <InformationNow :dn="dataNow" />
    </div>
    <div v-else-if="selected–°ard == 2">
      <InformationForecast :df="dataForecast" />
    </div>
  </div>
</template>

<script>
import Header from "./components/Header.vue";
import Card from "./components/Card.vue";
import InformationNow from "./components/InformationNow.vue";
import InformationForecast from "./components/InformationForecast.vue";
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap/dist/js/bootstrap.js";
import { DownloadInformationNow } from "./scripts/Download";
import { DownloadInformationForecast } from "./scripts/Download";
import { DownloadTextForecast } from "./scripts/Download";
import { GetLastItems } from "./scripts/Download";

export default {
  name: "App",
  data() {
    return {
      dataNow: "",
      dataForecast: "",
      selected–°ard: "",
      lastNow: "",
      lastForecast: "",
      textForecast: "",
      load: "",
    };
  },
  async mounted() {
    /* –ü–æ–ª—É—á–µ–Ω–∏–µ —Ç–µ–∫—Å—Ç–æ–≤–æ–≥–æ –ø—Ä–æ–≥–Ω–æ–∑–∞ */
    DownloadTextForecast().then((e) => {
      this.textForecast = e;
    });
    /* –ü–æ–ª—É—á–µ–Ω–∏–µ –¥–∞–Ω–Ω—ã—Ö –∑–∞ —Ç—Ä–æ–µ —Å—É—Ç–æ–∫ */
    DownloadInformationNow().then((e) => {
      this.dataNow = e;
      this.selected–°ard = 1;
      GetLastItems(e).then((e) => {
        this.lastNow = e;
        this.load = true;
      });
    });
    /* –ø–æ–ª—É—á–µ–Ω–∏–µ –ø—Ä–æ–≥–Ω–æ–∑–∞ –Ω–∞ —Ç—Ä–æ–µ —Å—É—Ç–æ–∫ */
    DownloadInformationForecast().then((e) => {
      this.dataForecast = e;
    });
  },

  methods: {
    change: function (id) {
      this.selected–°ard = id;
    },
  },
  components: {
    Header,
    Card,
    InformationNow,
    InformationForecast,
  },
};
</script>

