<script>
import { Bar } from "vue-chartjs";
import { Style } from "../scripts/Download";

export default {
  extends: Bar,
  props: ["data"],
  data() {
    return {
      options: {
        legend: {
          display: this.data.type == "windspeed" ? true : false,
        },
        responsive: !0,
        maintainAspectRatio: !1,
        scales: {
          xAxes: [
            {
              stacked: this.data.type == "windspeed" ? true : false,
              ticks: {
                fontSize: "14",
                fontColor: "black",
                callback: (value, index) => {
                  if (this.data.forecast == false) {
                    if (index % 3 == 0) {
                      if (value.split(" ")[1] == "00:00:00") {
                        return value.split(" ")[0];
                      } else {
                        return value.split(" ")[1].replace(/[:0]/g, "") + "—á";
                      }
                    }
                  } else {
                    if (index % 2 == 0) {
                      if (value.split(" ")[1] == "01:00:00") {
                        return value.split(" ")[0];
                      } else {
                         return value.split(" ")[1].replace(/[:0]/g, "") + "—á";
                      }
                    }
                  }
                },
              },
            },
          ],
          yAxes: [
            {
              ticks: { fontSize: "14", fontColor: "black" },
              stacked: this.data.type == "windspeed" ? true : false,
            },
          ],
        },
      },
    };
  },
  mounted() {
    Style(this.data).then((e) => {
      this.renderChart(e, this.options);
    });
  },
};
</script>