<script>
import { Radar } from "vue-chartjs";
import { Style } from "../scripts/Download";
export default {
  extends: Radar,
  props: ["data"],
  data() {
    return {
      options: {
        scale: {
          ticks: {
            beginAtZero: false,
            min: 0,
            stepSize: 2,
          },
        },
        responsive: !0,
        maintainAspectRatio: !1,
        tooltips: {
          enabled: false,
        },
        legend: {
          display: false,
        },
      },
    };
  },
  mounted: function () {
    Style(this.data).then((e) => {
      this.renderChart(e, this.options);
    });
  },
};
</script>