
/* 99 –î–∞–≤–ª–µ–Ω–∏–µ, 100 –í–ª–∞–∂–Ω–æ—Å—Ç—å, 101 –ù–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –≤–µ—Ç—Ä–∞, 102 –°–∫–æ—Ä–æ—Å—Ç—å –≤–µ—Ç—Ä–∞, 103 –¢–µ–º–ø–µ—Ä–∞—Ç—É—Ä–∞, 369 –ü–æ—Ä—ã–≤—ã */
/* –ö–æ–¥—ã –¥–∞–Ω–Ω—ã—Ö */
const indicators = '99,100,101,102,103,369';
const date = new Date();
const year = date.getFullYear();
const mouth = date.getMonth() + 1;
const day = date.getDate();
const timeEnd = `${year}-${mouth}-${day}`;
const timeEndForecast = `${year}-${mouth}-${day + 2}`;
const timeBegin = `${year}-${mouth}-${day - 2}`;
const timeBeginForecast = `${year}-${mouth}-${day}`;
/* –ü–æ–ª—É—á–µ–Ω–∏–µ –¥–∞–Ω–Ω—ã—Ö –∑–∞ —Ç—Ä–æ–µ —Å—É—Ç–æ–∫ */
async function DownloadInformationNow() {
    const URL = `https://gis.krasn.ru/sc/api/1.0/projects/6/aggvalues?&sites=4203&key=f6q6kbspmw7p47gr&time_begin=${timeBegin}&time_end=${timeEnd} 23:00:00&time_interval=1hour&indicators=${indicators}`;
    const responce = fetch(URL);
    const result = responce.then(e => XML2JSON(e));
    return result;
}
/* –ü–æ–ª—É—á–µ–Ω–∏–µ –ø—Ä–æ–≥–Ω–æ–∑–∞ –Ω–∞ —Ç—Ä–æ–µ —Å—É—Ç–æ–∫ */
async function DownloadInformationForecast() {
    const URL = `https://gis.krasn.ru/sc/api/1.0/projects/10/aggvalues?&sites=4226&key=f6q6kbspmw7p47gr&time_begin=${timeBeginForecast}&time_end=${timeEndForecast} 23:00:00&time_interval=1hour&indicators=${indicators}`;
    const responce = fetch(URL);
    const result = responce.then(e => XML2JSON(e));
    return result;
}
/* –ü–æ–ª—É—á–µ–Ω–∏–µ —Ç–µ–∫—Å—Ç–æ–≤–æ–≥–æ –ø—Ä–æ–≥–Ω–æ–∑–∞ */
async function DownloadTextForecast() {
    const URL_TEXT = `https://gis.krasn.ru/sc/api/1.0/projects/10/values.xml?&time_begin=${timeBeginForecast}&time_end=${timeEndForecast} 23:00:00&key=f6q6kbspmw7p47gr&indicators=367`
    const responceText = fetch(URL_TEXT);
    const resultText = responceText.then(e => XML2TextForecast(e));
    return resultText;
}
/* –ü—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–Ω–∏–µ –¥–∞–Ω–Ω—ã—Ö */
async function XML2JSON(e) {
    const temperature = { time: [], values: [], name: "–¢–µ–º–ø–µ—Ä–∞—Ç—É—Ä–∞", type: "temperature" };
    const pressure = { time: [], values: [], name: "–î–∞–≤–ª–µ–Ω–∏–µ", type: "pressure" };
    const humidity = { time: [], values: [], name: "–í–ª–∞–∂–Ω–æ—Å—Ç—å", type: "humidity" };
    const winddirect = { time: [], values: [], name: "–ù–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –≤–µ—Ç—Ä–∞", type: "winddirect" };
    const windspeed = { time: [], values: { s: [], r: [] }, name: "–°–∫–æ—Ä–æ—Å—Ç—å –≤–µ—Ç—Ä–∞", type: "windspeed" };
    const r = e.text().then(e => {
        const dom = new DOMParser();
        const xml = dom.parseFromString(e, "text/xml");
        const aggvalues = xml.getElementsByTagName("aggvalue");
        let dayBegin = "";
        let count = 0;
        aggvalues.forEach((element) => {
            const indicator = element.getAttribute("indicator")
            const time = element.getAttribute("time");
            const value = Number(element.getElementsByTagName("avg")[0].innerHTML).toFixed(1);
            switch (indicator) {
                case '99':
                    pressure.time.push(
                        time
                    );
                    pressure.values.push(
                        value
                    )
                    break;
                case '100':
                    humidity.time.push(
                        time
                    );
                    humidity.values.push(
                        value
                    );
                    break;
                case '101':
                    if (time.split(' ')[0] != dayBegin) {
                        dayBegin = time.split(' ')[0]
                        winddirect.values.push([]);
                        winddirect.time.push(
                            time
                        );
                        count++;
                    }
                    winddirect.values[count - 1].push(
                        value
                    );
                    break;
                case '102':
                    windspeed.time.push(
                        time
                    );
                    windspeed.values.s.push(
                        value
                    );
                    break;
                case '103':
                    temperature.time.push(
                        time
                    );
                    temperature.values.push(
                        value
                    );
                    break;
                case '369':
                    windspeed.values.r.push(
                        value
                    );
                    break;
                default:
                    console.log("–ù–µ—Ç —Ç–∞–∫–∏—Ö –∑–Ω–∞—á–µ–Ω–∏–π");
            }
        })
        return [
            temperature, windspeed, pressure, humidity, winddirect
        ]
    })
    /*  console.log(await r); */
    return r;
}
/* –ü—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–Ω–∏–µ —Ç–µ–∫—Å—Ç–æ–≤—ã—Ö –¥–∞–Ω–Ω—ã—Ö */
async function XML2TextForecast(e) {
    const ugms = [];
    const russia = [];
    const r = e.text().then(e => {
        const dom = new DOMParser();
        const xml = dom.parseFromString(e, "text/xml");
        const values = xml.getElementsByTagName("value");
        values.forEach((element) => {
            const time = element.getAttribute("time")
            const value = element.innerHTML
            if (time.split(' ')[1] == "12:01:00") {
                ugms.push({
                    time: time.split(' ')[0],
                    value: value
                })
            }
            if (time.split(' ')[1] == "12:02:00") {
                russia.push(
                    {
                        time: time.split(' ')[0],
                        value: value
                    }
                );
            }
        })
        return { ugms, russia }
    })
    return r;
}
/* –ü—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–Ω–∏–µ –æ–±—Ä–∞–±–æ—Ç–∞–Ω–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –Ω–∞ –≥—Ä–∞—Ñ–∏–∫–µ */
async function Style(e) {
    const dir = [
        "–°",
        "–°–°–í",
        "–°–í",
        "–í–í–°",
        "–í",
        "–í–Æ–í",
        "–Æ–í",
        "–Æ–Æ–í",
        "–Æ",
        "–Æ–Æ–ó",
        "–Æ–ó",
        "–ó–Æ–ó",
        "–ó",
        "–ó–°–ó",
        "–°–ó",
        "–°–°–ó",
    ]
    const chartdata = {
        labels: (e.type == "winddirect") ? dir : e.time,
        datasets: []
    }
    if (e.type == "temperature") {
        chartdata.datasets = [
            {
                label: "–¢–µ–º–ø–µ—Ä–∞—Ç—É—Ä–∞",
                backgroundColor: "#00BFFF",
                data: e.values
            }
        ]
    }
    if (e.type == "pressure") {
        chartdata.datasets = [
            {
                label: "–î–∞–≤–ª–µ–Ω–∏–µ",
                backgroundColor: "#DDA0DD",
                data: e.values
            }
        ]
    }
    if (e.type == "humidity") {
        chartdata.datasets = [
            {
                label: "–í–ª–∞–∂–Ω–æ—Å—Ç—å",
                backgroundColor: "#1E90FF",
                data: e.values
            }
        ]
    }
    if (e.type == "winddirect") {
        CountDirections(e.values).then(e => {
            chartdata.datasets = [
                {
                    label: "–ù–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –≤–µ—Ç—Ä–∞",
                    backgroundColor: "#20B2AA",
                    data: e
                }
            ]
        })
    }
    if (e.type == "windspeed") {
        chartdata.datasets = [
            {
                label: "–°–∫–æ—Ä–æ—Å—Ç—å –≤–µ—Ç—Ä–∞",
                backgroundColor: "#008B8B",
                data: e.values.s
            },
            {
                label: "–ü–æ—Ä—ã–≤—ã –≤–µ—Ç—Ä–∞",
                backgroundColor: "#00CED1",
                data: e.values.r
            }

        ]
    }
    return chartdata;
}
/* –†—É—Å—Å–∫–∏–µ –Ω–∞–∑–≤–∞–Ω–∏—è –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–π –ø–æ –≥—Ä–∞–¥—É—Å–∞–º */
function DirectionText(value) {
    if (value < 0.3)
        return "–®—Ç–∏–ª—å"
    if (value >= 0.3 && value < 22)
        return "–°"
    if (value >= 22 && value < 45)
        return "–°–°–í"
    if (value >= 45 && value < 67)
        return "–°–í"
    if (value >= 67 && value < 90)
        return "–í–í–°"
    if (value >= 90 && value < 112)
        return "–í"
    if (value >= 112 && value < 135)
        return "–í–Æ–í"
    if (value >= 135 && value < 157)
        return "–Æ–í"
    if (value >= 157 && value < 180)
        return "–Æ–Æ–í"
    if (value >= 180 && value < 202)
        return "–Æ"
    if (value >= 202 && value < 225)
        return "–Æ–Æ–ó"
    if (value >= 225 && value < 247)
        return "–Æ–ó"
    if (value >= 247 && value < 270)
        return "–ó–Æ–ó"
    if (value >= 270 && value < 292)
        return "–ó"
    if (value >= 292 && value < 315)
        return "–ó–°–ó"
    if (value >= 315 && value < 337)
        return "–°–ó"
    if (value >= 337 && value < 360)
        return "–°–°–ó"
    if (value == 360)
        return "–°"
}
/* –ü–æ–¥—Å—á–µ—Ç –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–π */
async function CountDirections(e) {
    const text = [
        "–°",
        "–°–°–í",
        "–°–í",
        "–í–í–°",
        "–í",
        "–í–Æ–í",
        "–Æ–í",
        "–Æ–Æ–í",
        "–Æ–Æ–ó",
        "–Æ–ó",
        "–ó–Æ–ó",
        "–ó",
        "–ó–°–ó",
        "–°–ó",
        "–°–°–ó",
        "–°",
    ];
    const direction = [];
    e.forEach(element => {
        direction.push(DirectionText(element));
    })
    const counts = direction.reduce(function (acc, el) {
        acc[el] = (acc[el] || 0) + 1;
        return acc;
    }, {});
    const result = [];
    text.forEach(i => {
        (counts[i]) ? result.push(counts[i]) : result.push(0)
    });
    return result
}
/* –ü–æ–ª—É—á–∏—Ç—å –ø–æ—Å–ª–µ–¥–Ω–∏–π —ç–ª–µ–º–µ–Ω—Ç */
async function GetLastItems(e) {
    let result = {};
    let length = "";
    for (let key in e) {
        switch (e[key].type) {
            case 'windspeed':
                length = e[key].values.s.length;
                result[e[key].type] = { s: (e[key].values.s[length - 1] > 0.2) ? e[key].values.s[length - 1] : 0, r: e[key].values.r[length - 1], time: DateToText(e[key].time[length - 1]) }
                break;
            case 'winddirect':
                length = e[key].values[2].length;
                result[e[key].type] = [e[key].values[2][length - 1], DirectionText(e[key].values[2][length - 1])];
                break;
            default:
                length = e[key].values.length;
                result[e[key].type] = e[key].values[length - 1];
                break;
        }
    }
    return result;
}
/* –ü–æ–ª—É—á–∏—Ç—å –ü–µ—Ä–≤—ã–π —ç–ª–µ–º–µ–Ω—Ç */
async function GetFirstItems(e) {
    const result = [];
    for (let key in e) {
        console.log();
        switch (e[key].type) {
            case 'windspeed':
                result.push({ name: e[key].name, value: e[key].values.s[0], time: e[key].time[0] });
                break;
            case 'winddirect':
                result.push({ name: e[key].name, value: DirectionText(e[key].values[0][0]), time: e[key].time[0] });
                break;
            default:
                result.push({ name: e[key].name, value: e[key].values[0], time: e[key].time[0] });
        }

    }
    return result;
}

function DateToText(e) {
    const date = e.split(' ')[0].split('-');
    const hour = e.split(' ')[1].split(':');
    const result = new Date(date[0], date[1] - 1, date[2], hour[0], 0, 0).toLocaleString('ru', {
        month: 'long',
        day: 'numeric',
        weekday: 'long',
        hour: 'numeric',
        minute: 'numeric',
    });
    return result
}

export { DownloadInformationNow, DownloadInformationForecast, DownloadTextForecast, Style, GetLastItems, GetFirstItems, DateToText }